import _ from 'lodash';
import React from 'react';
import { graphql } from 'gatsby';
import Layout from "../components/layout";
import Breadcrumb from '../components/breadcrumb';
import YearSelect from '../components/year-selector';
import GameRecord from '../components/GameRecord';
import MatchupRecord from '../components/MatchupRecord';
//import { gameOutcome } from '../helpers/game';

class IndexPage extends React.Component {
	constructor(props) {
						super(props);
						this.yearChangeHandler = this.yearChangeHandler.bind(this);
						this.loadYearData = this.loadYearData.bind(this);
	        	this.state = {
								topScores: [],
								bottomScores: [],
								yearLabels: [],
								YearSelected: "",
								gamesByYear: []

		        }
	        }
					
		yearChangeHandler = (event) => {
			var selected = event.target.value;
			this.setState(
				{ YearSelected: selected },
				() => this.loadYearData()
			)
		}
	
		loadYearData = () => {
			const owners = this.props.data.allDataJson.edges[0].node.owners;
			var year = this.state.YearSelected;
			var includeRegularSeason = this.refs.regular_season.checked;
			var includePlayoffs = this.refs.playoffs.checked;

			var allYearsObject = this.state.gamesByYear;
			var yearObject = this.state.gamesByYear.find(obj => obj.year === year);
			var gameWeeks = [];
			var allGamesYear = [];
			var allScores = [];

			if (year === 'All') {
				allYearsObject.forEach(function (element) {
					let weeks = element.weeks;
					// Just regular season
					if (!includePlayoffs && includeRegularSeason) { 
						weeks = _.filter(weeks, { 'isPlayoff': false });
						gameWeeks.push(weeks);
					}

					//Just playoffs 
					else if (includePlayoffs && !includeRegularSeason) {
						weeks = _.filter(weeks, { 'isPlayoff': true });
						gameWeeks.push(weeks);
					}
					// All
					else if (includePlayoffs && includeRegularSeason) {
						gameWeeks.push(weeks);
					}		
				});
			}
			else {
				let weeks = yearObject.weeks;
				// Just regular season
				if (!includePlayoffs && includeRegularSeason) {
					weeks = _.filter(weeks, { 'isPlayoff': false });
					gameWeeks = weeks;
				}
				//Just playoffs 
				else if (includePlayoffs && !includeRegularSeason) {
					weeks = _.filter(weeks, { 'isPlayoff': true });
					gameWeeks = weeks;
				}
				// All
				else if (includePlayoffs && includeRegularSeason) {
					gameWeeks = weeks;
				}
			}
			

			gameWeeks.reverse();

			//get all games from weeks for each year
			if (year === 'All') {
				for (let i = 0; i < gameWeeks.length; i++) {
					gameWeeks[i].forEach((week) => {
						week.games.forEach((game) => {
							allGamesYear.push(game);
							allScores.push({
								id: game.team_a["0"].ownerId,
								name: game.team_a["0"].owner,
								score: game.team_a["0"].score,
								week: game.week,
								year: game.year,
								image: _.find(owners, ['id', game.team_a["0"].ownerId]).image
							});
							allScores.push({
								id: game.team_b["0"].ownerId,
								name: game.team_b["0"].owner,
								score: game.team_b["0"].score,
								week: game.week,
								year: game.year,
								image: _.find(owners, ['id', game.team_b["0"].ownerId]).image
							});
						})
					})
				}
			}
			// Get games for just one year
			else {
				gameWeeks.forEach((week) => {
					week.games.forEach((game) => {
						allGamesYear.push(game);
						allScores.push({
							id: game.team_a["0"].ownerId,
							name: game.team_a["0"].owner,
							score: game.team_a["0"].score,
							week: game.week,
							year: game.year,
							image: _.find(owners, ['id', game.team_a["0"].ownerId]).image
						});
						allScores.push({
							id: game.team_b["0"].ownerId,
							name: game.team_b["0"].owner,
							score: game.team_b["0"].score,
							week: game.week,
							year: game.year,
							image: _.find(owners, ['id', game.team_b["0"].ownerId]).image
						});
					})
				})
			}

			allScores = _.sortBy(allScores, ['score']);
			var topScores = _.orderBy(_.takeRight(allScores, 10), ['score'], ['desc']);
			var bottomScores = _.take(allScores, 10);

			this.setState({ topScores: topScores });
			this.setState({ bottomScores: bottomScores });
			
		}
	
		UNSAFE_componentWillMount() {
			const years = this.props.data.allDataJson.edges[0].node.years;
			var games = [];
			
			years.forEach((year) => {
				year.weeks_games.forEach((week) => {
					games.push(...week.games);
				})
			});

			var playOffWeeks = [];
			var gamesByYear = [];
			var yearLabels = [];
			
			// get games by years and weeks

			games.forEach((game) => {
				// If year repeats itself
				if(gamesByYear.find(obj => obj.year === game.year)) { 
					var currentYear = gamesByYear.find(obj => obj.year === game.year)
						if (currentYear.weeks.find(obj => obj.week === game.week)) {
							var currentWeek = currentYear.weeks.find(obj => obj.week === game.week)
							currentWeek.games.push(game);
						} else {
							currentYear.weeks.push({
								week: game.week,
								games: [game]
							})
						}
				} else { // Initial year 
					gamesByYear.push({
							year: game.year,
							weeks: [{
								week: game.week,
								games: [game]
							}]
					});
					yearLabels.push(game.year);
				}
			});

			// mark the playoff weeks and populate array
			gamesByYear.forEach((year) => {
				year.weeks.forEach((week) => {
					if (week.games[0].playoffTierType !== 'NONE') {
						week.isPlayoff = true;
						playOffWeeks.push(week)
					}
					else {
						week.isPlayoff = false;
					}
				});
			});
			
			yearLabels.reverse();

			this.setState({yearLabels:yearLabels });
		
			this.setState({gamesByYear:gamesByYear})
			this.setState({YearSelected: 'All' })
			 
  	}        
  	
  componentDidMount() {
		this.loadYearData();
	}
		
	regularSeasonClickHandler = event => {
		this.loadYearData();
	}
		
  	render() {
	
		 return (
			 <Layout >
				<main className="container-fluid">
					<Breadcrumb title="Game Records"/>
					<div className="card mb-3">
							<div className="card-header"> 
								<input type="checkbox" id="regular-season" ref="regular_season" value="regular-season" onChange={this.regularSeasonClickHandler} defaultChecked={true} /> <span className="checkbox-label">Regular Season</span>
								<input type="checkbox" id="playoffs" ref="playoffs" value="playoffs" onChange={this.regularSeasonClickHandler} /> <span className="checkbox-label">Playoffs</span>
								<br/><br/>
								<YearSelect data={this.state.yearLabels} change={(event) => this.yearChangeHandler(event)}/>
							</div>
							<div className="card-body">
								<div className="row">
									<GameRecord data={this.state.topScores} label="Score" title="Top Ten Scores"  />
									<GameRecord data={this.state.bottomScores} label="Score" title="Bottom Ten Scores" />
								</div>
								<div className="row my-4">
									<MatchupRecord data={this.state.topScores} label="Margin" title="Top Ten Victory Margins" />
									<MatchupRecord data={this.state.bottomScores} label="Margin" title="Bottom Ten Victory Margins" />
								</div>
							</div>
							<div className="card-footer small text-muted"></div>
						</div>
				</main>
			 </Layout>
			 );
	}
}
export default IndexPage;

export const pageQuery = graphql`
  query teamRecordsQuery {
		allDataJson {
			edges {
				node {
					owners {
						name
						fantasyname
						id
						image
						status
					}
					years{
						weeks_games {
							games {
								week
								playoffTierType
								winner
								team_a {
									name
									owner
									ownerId
									logo
									score
									rank
								}
								team_b {
									name
									owner
									ownerId
									logo
									score
									rank
								}
								year
							}
						}
					}
				} 
			}
		}
}
`;
